@import '../../sass/utils/mixin';

.languageSelect {
  position: relative;
  display: inline-block;
  font-size: 16px;

  .selected {
    border-radius: 4px;
    border: none;
    color: rgba(240, 231, 218, 1);
    background: rgba(39, 31, 28, 1);
    width: 80px;
    height: 40px;
    cursor: pointer;
    display: flex;
    transition: 0.3s;
  }

  .arrow {
    font-size: 12px;
  }

  .dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: rgba(240, 231, 218, 1);
    border: 1px solid rgba(191, 119, 66, 1);
    border-radius: 8px;
    padding: 0;
    z-index: 10;
  }

  .option {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        text-align: left;
        text-underline-position: from-font;
        text-decoration-skip-ink: none;
    text-align: center;
    padding: 8px 12px;
    cursor: pointer;
    transition: 0.3s;
    color: rgba(39, 18, 12, 1);

    &:hover,
    &.active {
      color: rgba(191, 119, 66, 1);
    }
  }
}

.currentLanguage {
  padding: 8px 4px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
}

.iconSelectLaunguage {
  width: 40px;
  height: 40px;
}
